.button_flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.button {
  width: 120px;
  margin: 8px 16px;
}

.tag {
  margin: 16px;
}

.tag_flex {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
}
.usBoxContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  align-content: flex-start;
  margin: 8px;
}

.hoverCard {
  transition: all 0.7s;
  border: 2px solid transparent;
}

.hoverCard:hover {
  border: 2px solid rgb(240, 77, 77);
}

.usBox {
  cursor: pointer;
  margin: 8px;
  width: 240px;
  background-color: #111;
  margin: 8px auto;
  padding: 16px;
  border-radius: 8px;
  box-shadow: 1px 2px 10px 0px #333;
  height: 120px;
  transition: all 0.8s;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  flex-wrap: wrap;
  align-content: space-between;
}
.usBox:hover {
  box-shadow: 1px 5px 20px 0px #222;
}

.usBoxRow {
  padding: 4px;
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}

.formControll {
  /* padding: 8px; */
  margin-bottom: 16px;
}

.formControll input,
.formControll textarea {
  /* padding: 8px; */
  margin-left: 8px;
}
.modalContainer {
  max-width: 670px;
  width: 80% !important;

  margin: 0 auto;
  min-width: 300px;
}
.accept {
  width: calc(100% - 156px);
}

.borderi {
  /* border: 1px solid rgba(197, 197, 197, 0.342); */
  background-color: #272727;
  padding: 16px;
  border-radius: 8px;
}
.borderi::before {
  content: attr(data-name);
  display: block;
  background-color: #272727;
  width: min-content;
  padding-left: 10px;
  padding-top:4px ;
  padding-right: 10px;
  border-radius: 8px;
  /* height: 20px; */
  /* position: absolute; */
  transform: translateY(-29px);
  display: block;
  /* left: 8px;
  top: -16px; */
}
